import React,{createContext,useState} from "react";
import dress from "../assets/dress.jpg";
import frock from "../assets/frock.jpg";
import gown from "../assets/gown.jpg";
import midi from "../assets/midi.jpg";
import short from "../assets/short.jpg";
import suit from "../assets/suit.jpg";





export const ProductsContext=createContext();

const ProductsContextProvider=(props)=>{
     const[products]=useState([
        {id:1,name:"dress",price:300,image: dress},
        {id:2,name:"frock",price:400,image: frock},
        {id:3,name:"gown",price:400,image: gown},
        {id:4,name:"midi",price:400,image: midi},
        {id:5,name:"short",price:400,image: short},
        {id:6,name:"suit",price:400,image: suit}

        
     ]);
    return(
   <ProductsContext.Provider value={{products: [...products]}}>
        {props.children}
   </ProductsContext.Provider>
    )
}

export default ProductsContextProvider;

