const Home = () =>{
    return(
        <>
        
         <div className="container">
        <Banner/>
        </div>
        
         <div className="product-image1"> 
            <img src={Boy} alt="not found" />
            <img src={Girl} alt="not found"/>
            </div> 
            
             
        
        </>
    )
}
export default Home;