import React from "react";
import {Link} from "react-router-dom";
const Navbar=()=>{
    return(
<nav>
     <ul className="left">
        <li>
        <Link to="/">Language</Link>
        </li>
    </ul>
    <ul className="left">
        <li>
            USD
        </li>
    </ul>

    <ul className="center">
        <li>
            
                <input type="text" placeholder="Search" ></input>
            
        </li>
    </ul>
    <ul className="right">
        <li>
        
                <span className="shoppingCart"><i class="far fa-heart"></i></span>
                 <span className="cart">WISHLIST</span>
                  
        </li>
    </ul>
    <ul className="right">
        <li>
        <Link to="cart">
                <span className="shoppingCart"><i class="fas fa-cart-plus"></i></span>
                 <span className="cart">CART</span>
                 </Link> 
        </li>
    </ul>
    
</nav>
    )
}
export default Navbar;